  font-family: "Noto Serif JP", serif;
  cursor: pointer;
}
.sub .accordion__btn::after {
  content: "";
  display: block;
  width: clamp(15px, calc(5px + 2.5641025641vw), 45px);
  aspect-ratio: 11/10;
  background: var(--base-color);
  clip-path: polygon(50% 100%, 0 0, 100% 0);
}
.sub .accordion__body {
  display: grid;
  grid-template-rows: 0fr;
  transition: 250ms grid-template-rows ease, 250ms padding-block ease;
}
.sub .accordion__body > div {
  overflow: hidden;
}
.sub .accordion__body[aria-hidden=false] {
  grid-template-rows: 1fr;
  padding-block: clamp(20px, calc(3.6666666667px + 4.188034188vw), 69px) 0;
}
.sub .accordion__text {
  text-align: center;
}